import React, { useState } from "react";
import { useToast } from "../../hooks/useToast";
import "react-toastify/dist/ReactToastify.css";
import "./LoginUser.css";
import { useNavigate } from "react-router-dom";

function LoginUser() {
  const { showSuccess, showError } = useToast();
  const [formData, setFormData] = useState({
    username: "",
    password: "",
  });
  const navigate = useNavigate();

  const handleChange = (e) => {
    setFormData((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch(
        "http://localhost:8000/routes/login_user.php",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json", // Make sure to send this
          },
          body: JSON.stringify(formData),
        }
      );
      const result = await response.json();
      handleUserLogin(result);
    } catch (error) {
      handleUserLogin({
        status: "error",
        errors: { general: "Something went wrong!" },
      });
    }
  };

  const handleUserLogin = (response) => {
    if (response.status === "error") {
      const allErrors = Object.values(response.errors || {}).join(" | ");
      showError(allErrors); // Show error using custom hook
    } else if (response.status === "success") {
      setTimeout(() => {
        navigate("/home");
      }, 1500);
      showSuccess(response.message); // Show success using custom hook
    }
  };

  return (
    <div className="login-container">
      <form className="login-form" onSubmit={handleSubmit}>
        <h2 className="login-title">Login</h2>

        <input
          type="text"
          name="username"
          placeholder="Username"
          value={formData.username}
          onChange={handleChange}
          className="input-field"
          required
        />

        <input
          type="password"
          name="password"
          placeholder="Password"
          value={formData.password}
          onChange={handleChange}
          className="input-field"
          required
        />

        <button type="submit" className="submit-btn">
          Login
        </button>
      </form>
    </div>
  );
}

export default LoginUser;
