import React, { useEffect, useState } from "react";
import "./LandingPage.css";
import LoginUser from "../Login/LoginUser";
import SignupForm from "../Signup/SignupUser";
import $ from "jquery";

function LandingPage() {
  useEffect(() => {
    // Check if jQuery is loaded
    if (typeof $ !== "undefined") {
      console.log("jQuery is loaded!");
    } else {
      console.error("jQuery is not loaded!");
    }
  }, []);
  const [isLogin, setIsLogin] = useState(false); // State to manage the toggle

  const handleToggle = () => {
    setIsLogin(!isLogin); // Toggle the state between login and signup
  };

  return (
    <div className="landing-container">
      <h1>Welcome!</h1>

      {/* Toggle Button to switch between Login and Signup */}
      <div className="toggle-buttons">
        <button onClick={handleToggle} className="btn">
          {isLogin ? "Go to Sign Up" : "Go to Login"}
        </button>
      </div>

      {/* Render the appropriate form based on isLogin state */}
      <div className="form-container">
        {isLogin ? <LoginUser /> : <SignupForm />}
      </div>
    </div>
  );
}

export default LandingPage;
