import React, { useEffect } from "react";
import $ from "jquery"; // Ensure jQuery is imported
import "jvectormap"; // jVectorMap JS import
import "jvectormap/jquery-jvectormap.css";

const PakMap = () => {
  useEffect(() => {
    console.log("Loading TopoJSON from:", "../PAK-TopoJSON/PAK_adm0.json");

    // Load the custom TopoJSON file and initialize the map
    $.getJSON("../PAK-TopoJSON/PAK_adm0.json", function (data) {
      console.log("TopoJSON loaded", data);

      $("#map").vectorMap({
        map: "custom/pakistan", // Custom map name
        backgroundColor: "#a4bac7",
        series: {
          regions: [
            {
              values: {
                "PK-BA": "#FF0000", // Example color for regions
                "PK-GB": "#00FF00",
                "PK-KP": "#0000FF",
                "PK-PB": "#FFFF00",
                "PK-SD": "#FF00FF",
              },
              scale: ["#C8EEFF", "#006491"],
              normalizeFunction: "polynomial",
            },
          ],
        },
        onRegionClick: function (event, code, region) {
          alert("You clicked on " + region);
        },
      });
    }).fail(function () {
      console.error("Failed to load the TopoJSON file.");
    });
  }, []);

  return (
    <div>
      <h2 style={{ textAlign: "center", marginBottom: "1rem" }}>Map of</h2>
      <div id="map" style={{ width: "100%", height: "500px" }}></div>
    </div>
  );
};

export default PakMap;
